* {
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
  color: #deebf8;
}

body {
  margin: 0;
  background-color: #282d35;
}

/*=============Nav section=============*/
nav {
  background-color: #21222a;
  height: 90px;
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-right: 50px;
  margin-left: 50px;
  height: 90px;
}

.logo {
  display: flex;
  text-align: center;
  justify-content: space-between;
}

.logo h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 10px;
  padding-left: 10px;
  margin-left: 15px;
  margin-right: 15px;
  color: #61dafb;
  font-weight: 700;
}

.navbar-items {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar-items ul {
  list-style: none;
}

.navbar-items li {
  float: left;
}

.navbar-items li a {
  padding-right: 16px;
  padding-left: 16px;
  text-decoration: none;
}

.navbar-items li a:hover {
  background-color: #111111;
}

/*=============Main section=============*/
main {
  background-image: url(./images/React-gray-icon.svg);
  background-repeat: no-repeat;
  background-position-x: calc(100% + 205px);
  background-position-y: 100%;
}

.container-main {
  width: 500px;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 100px;
}

.container-main .main-title {
  font-size: 39px;
  margin: 0;
}

.container-main ul {
  margin-top: 50px;
  max-width: 500px;
}

.container-main ul li {
  line-height: 19px;
  padding-block: 13px;
}

.container-main li::marker {
  color: #61dafb;
  font-size: 1.5rem;
}
